$primary-color: #FF7E00;
$text-gray: #9B9B9B;
$text-black: #000000;
$background-color: #F6F7F9;

$padding-32: 32px;
$padding-16: 16px;
$padding-8: 8px;
$nav-width: 382px;

$cubic-bezier-bounce: cubic-bezier(.05, .53, .42, 1.5);

$font-size-nav: 20px;

body {
  font-family: 'Roboto', sans-serif;
  background-color: $background-color;
  margin: 0;
  padding: 0;
}

a {
  color: $primary-color;
  text-decoration: none;
}

h1 {
  font-size: 20px;
  font-weight: bold;
  text-transform: uppercase;
  color: $text-gray;
}

h2 {
  font-weight: bold;
  font-size: 30px;
  color: $text-black;
}

h3 {
  font-size: 18px;
  color: $text-gray;
}

h4 {
  font-size: 120px;
  color: $primary-color;
}

p {
  font-size: 24px;
  font-weight: 300;
  color: $text-black;
}

.project-contents {
  p {
    font-size: 20px;
  }

  img {
    width: 100%;
  }
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

.nav-container {
  background: white;
  position: fixed;
  right: 0;
  height: 100%;
  width: $nav-width;
}

nav {
  display: flex;
  height: 100%;
  align-items: center;
  justify-content: center;
  text-align: center;

  li {
    text-transform: uppercase;
    font-weight: bold;
    padding: $padding-16;
  }

  a {
    color: $text-gray;
    text-decoration: none;
    font-size: $font-size-nav;
    transition: color 0.15s ease-in;
    cursor: pointer;
  }

  a:hover {
    color: #717171;
  }

  .selected {
    color: $primary-color;
  }

  .selected:hover {
    color: $primary-color;
  }
}

::selection {
  color: white;
  background-color: $primary-color;;
}

.round-button {
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: linear-gradient(270deg, #FF6812 0%, #FF913A 100%);
  border-radius: 33px;
  width: 66px;
  height: 66px;
  position: fixed;
  right: $nav-width;
  transform: translateX(33px) translateY(calc(50vh - 33px));
  cursor: pointer;
  transition: all 0.4s $cubic-bezier-bounce;
}

.round-button:hover {
  transform: translateX(33px) translateY(calc(50vh - 33px)) scale(1.1);
  user-select: none;
}

.round-button img {
  height: 30px;
  user-select: none;
  transition: all 0.75s $cubic-bezier-bounce;
}

.rotated-180 {
  transform: rotate(180deg);
}

.page {
  width: calc(100% - #{$nav-width});
  display: flex;
  justify-content: start;
  align-items: start;
  flex-direction: column;
}

.page-inner {
  width: 100%;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 15vh;
  padding: 0 $padding-32 100px $padding-32;
}

.portrait-picture {
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s $cubic-bezier-bounce;
  width: 250px;
}

.portrait-picture:hover {
  box-shadow: 4px 6px 39px -20px rgba(0, 0, 0, 0.75);
  transform: scale(1.05);
}

.contact-buttons {
  display: flex;
  align-items: flex-end;
  margin-bottom: 200px;

  svg {
    margin-right: $padding-16;
    width: 32px;
    transition: 0.2s $cubic-bezier-bounce;
  }

  svg path {
    fill: $text-gray;
  }

  svg:hover path {
    fill: $primary-color;
  }

  svg:hover {
    transform: scale(1.1);
  }
}

.skill-grid {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.skill-bar-container {
  margin-bottom: 24px;
  min-width: 300px;
  margin-right: $padding-32;

  .skill-bar-title {
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 10px;
  }

  .skill-bar-outer {
    height: 12px;
    background: #ECECEC;
    border-radius: 12px;
  }

  .skill-bar-inner {
    height: 12px;
    border-radius: 12px;
    width: 0;
    transition: width 1s ease-out;
    background-image: linear-gradient(90deg, #FFB500 3%, #FF7E00 97%);
  }
}

.goals-list {
  font-size: 24px;
  font-weight: 300;
  color: $text-black;
  list-style: disc;
  padding-left: 24px;
}

.goals-list li {
  list-style: inherit;
}

.tag-container {
  display: flex;
  flex-wrap: wrap;
}

.tag {
  background-color: $primary-color;
  color: white;
  padding: 7px 18px;
  border-radius: 15px;
  margin-right: 8px;
  margin-bottom: 8px;
  font-weight: bold;
  cursor: pointer;
  text-transform: uppercase;
  transition: all 0.2s $cubic-bezier-bounce;
}

.tag-more {
  background-color: #DADAE2;
  color: #585858;
}

.project-container {
  position: relative;
  margin-bottom: 80px;

  h2 {
    margin-bottom: 0;
  }
}


.project-content-enter {
  opacity: 0;
  max-height: 0px;
}
.project-content-enter-active {
  opacity: 1;
  max-height: 1000px;
  transition: all 300ms;
}
.project-content-exit {
  opacity: 1;
  max-height: 1000px;
}
.project-content-exit-active {
  opacity: 0;
  max-height: 0;
  transition: all 300ms;
}

.project-open-button-mobile {
  display: none !important;
}

.project-open-button {
  text-align: center;
  background-color: #EAEAEA;
  color: $text-gray;
  border-radius: 10px;
  font-weight: 500;
  cursor: pointer;
  font-size: 20px;
  user-select: none;
  top: 32px;
  text-transform: uppercase;
  transition: all 0.2s ease-in;
  display: flex;
  height: 32px;
  width: 32px;
  justify-content: center;
  align-items: center;
  padding: 10px;

  svg {
    transition: all 0.2s ease-in;
    width: 20px;
  }

  svg:hover path {
    fill: white;
  }
}

.project-open-button:hover {
  color: white;
  background-color: $primary-color;
  transform: scale(1.05);
}

.project-open-button-container {
  position: sticky;
  top: 100px;
  height: 0;
  transition: all 0.2s ease-in;

  .project-open-button {
    position: absolute;
    left: calc(-32px * 3);
  }
}


@media only screen and (max-width: 1200px) {
  .nav-container {
    display: none;
  }

  .page {
    width: unset;
  }

  .page-inner {
    width: unset;
    margin-top: $padding-32;
  }

  .skill-bar-container {
    margin-right: 0;
  }

  .contact-buttons {
    margin-bottom: 0;
  }

  .round-button {
    transform: none;
    right: $padding-32;
    top: unset;
    bottom: $padding-32;
    z-index: 2;
  }

  .round-button:hover {
    transform: scale(1.1);
    user-select: none;
  }

  .project-open-button-container {
    .project-open-button {
      display: none;
    }
  }

  .project-open-button-mobile {
    display: inline-flex !important;
    margin-bottom: $padding-16;
  }
}
